.add-catalog {
  @include list-reset;
}

.add-catalog__link {
  text-decoration: none;
  display: block;
}

.add-catalog__title {
  color: $special_dark-grey;
  font-family: "Oswald", sans-serif;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 16px;
  text-transform: uppercase;
  margin: 0;
}

.add-catalog__item {
  display: flex;
  flex-direction: column;
  gap: 13px;
  padding: 15px 0 19px;
  border-top: 1px solid $special_grey;

  &:last-child {
    border-bottom: 1px solid $special_grey;
  }
}

.add-catalog__descript {
  margin: 0;
  display: grid;
  grid-template-columns: 2fr 1fr;
}

