.add-catalog__victory {
  color: $white;
  text-align: center;
  font-family: "Lato", sans-serif;
  font-size: 16px;
  line-height: 20px;
  background: linear-gradient(to top, rgba(104, 183, 56, 0.85), rgba(104, 183, 56, 0.85)), url("../img/catalog/cat-catalog.png");
  background-repeat: no-repeat;
  height: 290px;
  margin: 0;
  margin-bottom: 35px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.add-catalog__victory-img {
  display: block;
  width: 78px;
  height: 80px;
  background: url("../../img/catalog/icon_gift.svg"), no-repeat;
  margin-top: 60px;
}

.add-catalog__victory-text {
  display: block;
  width: 161px;
  margin-top: 48px;
}

@media (min-width: $tablet-width) {
  .add-catalog__title {
    font-size: 20px;
    line-height: 20px;
  }

  .add-catalog__item {
    display: grid;
    grid-template-columns: 2fr 1fr;
    padding: 23px 0 26px;
  }

  .add-catalog__victory {
    margin: 70px 0 75px;
    flex-direction: row;
    height: 200px;
    justify-content: space-between;
    padding: 0 120px 0 75px;
  }

  .add-catalog__victory-img {
    margin-top: 0;
  }

  .add-catalog__victory-text {
    margin-top: 0;
    text-align: start;
  }
}

@media (min-width: $desktop-width) {
  .add-catalog__item {
    grid-template-columns: 1fr 1fr 1fr;
    align-items: center;
    gap: 40px;
    padding: 15px 0;
  }

  .add-catalog__victory {
    width: 245px;
    height: 288px;
    margin: 0;
    flex-direction: column;
    justify-content: start;
    align-items: center;
    padding: 60px 40px 40px;
  }

  .add-catalog__title {
    width: 330px;
  }

  .add-catalog__victory-img {
    margin-bottom: 35px;
  }

  .add-catalog__victory-text {
    text-align: center;
  }
}
